@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Lato&family=Open+Sans+Condensed:wght@700&family=Roboto&display=swap);
/* fonts */
html {
	box-sizing: border-box;
}
*,
*::before,
*::after {
	box-sizing: inherit;
}
body {
  font-family: 'Roboto', sans-serif;
}
html, body {
  height: 100%;
  margin: 0 auto;
}
#root {
  width: 100%;
  height: 100%;
  background-color: #EFEDED;
}
@media (max-width: 768px) {
  .toolbar__tool_settings {
      min-width: 20%;
      height: inherit;
      display: flex;
      align-items: center;
      justify-content: center;
  }
  .toolbar__tool_export{
      min-width: 20%;
      height: inherit;
      display: flex;
      align-items: center;
      justify-content: center;
  }
  /* show-down menu settings */
  .settings-panel{
      max-height: 500px;
      overflow-y: scroll;
      min-width: 20%;
      z-index: 90;
      margin: 0;
      background-color: #FFFCFC;
      position: absolute;
      display: flex;
      flex-direction: column;
      align-items: center;
  }
}
.wrapper {
    width: 100%;
    height: 100%;
    background-color: #EFEDED;
    position: fixed;
}

.toolbar{
    width: 100%;
    height: 60px;
    background-color: #A09CD3;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    z-index: 99;
}
.toolbar__tool_settings {
    width: 15%;
    height: inherit;
    display: flex;
    align-items: center;
    justify-content: center;
    -webkit-filter: drop-shadow(0px 6px 2px rgb(50, 73, 70));
            filter: drop-shadow(0px 6px 2px rgb(50, 73, 70));
    transition: all 1s ease;
    background: none;         
}
/* При нажатии на settings */
.open{
    background: linear-gradient(180deg, rgba(160, 156, 211, 0) 0%, #9CD3D3 100%);
}
.toolbar-settings-img {
    width: 30px;
    height: 30px;
    cursor: pointer;
}
.toolbar-settings-img:hover {
    -webkit-filter:drop-shadow(2px 2px 1px rgb(168, 255, 243));
            filter:drop-shadow(2px 2px 1px rgb(168, 255, 243))
}
.toolbar__tool_export{
    min-width: 15%;
    height: inherit;
    display: flex;
    align-items: center;
    justify-content: center;
}
.toolbar-export-img:hover{
    -webkit-filter:drop-shadow(2px 2px 2px rgb(168, 255, 243));
            filter:drop-shadow(2px 2px 2px rgb(168, 255, 243))
}
.toolbar__tool_add{
    height: inherit;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    padding: 0;
    margin: 0;
}
.tool{
    display: flex;
    justify-content: center;
    align-items: center;
}
.tool:nth-child(2), .tool:nth-child(3){
    margin-left: 20px;
}
.toolbar-cursor-img{
    width: 30px;
    height: 30px;
    align-items: center;
    cursor: pointer;
}
.active{
    -webkit-filter:drop-shadow(2px 2px 1px rgb(168, 255, 243));
            filter:drop-shadow(2px 2px 1px rgb(168, 255, 243))
}
.toolbar-cursor-img:hover{  
    -webkit-filter: drop-shadow(2px 2px 2px rgb(168, 255, 243));  
            filter: drop-shadow(2px 2px 2px rgb(168, 255, 243))
}
.toolbar-export-img{
    align-items: center;
    cursor: pointer;
}
.toolbar-text-img{
    width: 30px;
    height: 30px;
    align-items: center;
    cursor: pointer;
}
.toolbar-addimg-img{
    cursor: pointer;
}
.toolbar-text-img:hover{
    -webkit-filter: drop-shadow(2px 2px 2px rgb(168, 255, 243));
            filter: drop-shadow(2px 2px 2px rgb(168, 255, 243))
}
.toolbar-addimg-img:hover{
    -webkit-filter: drop-shadow(2px 2px 2px rgb(168, 255, 243));
            filter: drop-shadow(2px 2px 2px rgb(168, 255, 243))
}
.img-url-area{
    height: inherit;
    position: absolute;
    top: 60px;
    z-index: 999;
    background-color: #A09CD3;
    border-radius: 0 0 10px 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    display: none;
}
.img-url-area:nth-child(1){
    border: 2px black solid;
}
.urlArea{
    width: inherit;
    height: 20px;
    outline: none;
    -moz-appearance: none;
    background-color: rgb(198, 255, 251);
    border: none;
    border-radius: 0 0 5px 5px;
}
.export-params{
    position: absolute;
    top: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 999;
    background-color: #A09CD3;
    display: none;
    border-radius: 0 0 5px 5px;
}
.toolbar__export-type{
    display: flex;
    cursor: pointer;
    justify-content: center;
    align-items: center;
    border: 1px black solid;
    width: 100%;
}
.toolbar__export-type:hover{
    background-color: #dbd6d6;
}
.settings-panel{
    width: 15%;
    height: 100%;
    z-index: 90;
    margin: 0;
    font-size: 16px;
    background-color: #FFFCFC;
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-right: 1px solid #ddd2d2;
    opacity: 0;
    transition: .55s opacity, .55s visibility;
    visibility: hidden;
}
.visible {
    opacity: 1;
    visibility: visible;
}
.settings-panel__hs{
    width: 100%;
    height: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 2px solid #938B8B;
}
.size-params{
    font-size: 14px;
    width: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-bottom: 2px solid #938B8B;
}
.angle-block img{
    height: 12px;
    width: 17px;
}
.angle-params{
    display: flex;
    justify-content: center;
    margin-top: 20px;
    margin-bottom: 20px;
}
.width-height-params input{
    background-color: inherit;
    width: 50px;
    outline: none;
    border: none;
    text-align: center;
    padding: 0;
}
.width-height-params{
    margin-top: 20px;
    text-align: center;
}
.angle-value{
    background-color: inherit;
    width: 50px;
    outline: none;
    border: none;
    text-align: center;
    padding: 0;
}
.width-value:hover{
    background-color: #dbd6d6;
}
.height-value:hover{
    background-color: #dbd6d6;
}
.angle-value:hover{
    background-color: #dbd6d6;
}
.settings-color{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.color-picker{
    width: 100%;
    height: 100%;
    padding: 10px;
}
.color-picker:hover{
    cursor: pointer;
    -webkit-filter: drop-shadow(10px 0px 4px rgb(168, 255, 243));
            filter: drop-shadow(10px 0px 4px rgb(168, 255, 243));
}
.color-picker-area{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 96px;
    width: 100%;
    border-bottom: 1px solid #938B8B; 
}
.color-params{
    font-size: 14px;
    width: 100%;
    height: 48px;
    outline: none;
    border: none;
    text-align: center;
    padding: 0;
    border-bottom: 2px solid #938B8B;
}
.color-params:hover{
    background-color: #dbd6d6;
}
.font-style-form{
    outline: none;
    border: none;
    text-align: center;
    padding-bottom: 10px;
    margin-right: 10px;
    width: 70%;
    text-overflow: ellipsis;
}
.font-style-block{
    width: 100%;
    padding-bottom: 10px;
}
.font-style-form:hover{
    background-color: #dbd6d6;
}
.font-style-option{
    outline: none;
    border: none;
}
.text-params{
    font-size: 14px;
    width: 100%;
    padding-top: 20px;
    padding-bottom: 20px;
    border-bottom: 2px solid #938B8B;
    text-align: center;
}
.text-value{
    background-color: inherit;
    width: 50px;
    outline: none;
    border: none;
    text-align: center;
    padding: 0;
}
.text-value:hover{
    background-color: #dbd6d6;
}
.background-params{
    font-size: 14px;
    width: 100%;
    padding-top: 20px;
    padding-bottom: 20px;
    border-bottom: 2px solid #938B8B;
    text-align: center;
}
.background-repeat-block{
    width: 100%;
    padding-bottom: 10px;
}
.repeat-style-form{
    outline: none;
    border: none;
    text-align: center;
    text-overflow: ellipsis;
    width: 45%;
}
.repeat-style-form:hover{
    background-color: #dbd6d6;
}



.view {
    height: 100%;
    width: 100%;
    z-index: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    background-color: rgb(210 201 201);
    overflow: scroll;
}
/* banner */
.banner {
    position: relative;
    height: 70vh;
    width: 70vw;
    margin: 0;
    z-index: 0;
    overflow: hidden;
    background-size: 200px;
    background-repeat: no-repeat;
}
.banner__text{
    display: inline-block;
    position: relative;
    overflow: hidden;
    z-index: 0;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3; /* number of lines to show */
    line-height: 10vh;        /* fallback */
    height: 30vh;
    border: none;
    background-color: transparent;
    width: 100%;
    resize: none;
}
.move-text{
    width: 100%;
    height: 15px;
    position: absolute;
    background-color: #fff;
    border-radius: 0 0 20px 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(0, 0, 0);
    display: none;
    background: rgb(2,0,36);
    background: linear-gradient(190deg, rgba(2,0,36,1) 12%, rgba(23,255,255,1) 100%, rgba(9,9,121,0.196516106442577) 100%);

}

